@font-face {
  font-family: "Raleway";
  font-weight: 400;
  font-style: normal;
  src: local("Raleway"), url("../fonts/Raleway-Regular.woff2") format("woff2"), url("../fonts/Raleway-Regular.woff") format("woff");
}
@font-face {
  font-family: "Raleway";
  font-weight: 600;
  font-style: normal;
  src: local("Raleway SemiBold"), url("../fonts/Raleway-SemiBold.woff2") format("woff2"), url("../fonts/Raleway-SemiBold.woff") format("woff");
}
@font-face {
  font-family: "Raleway";
  font-weight: 700;
  font-style: normal;
  src: local("Raleway Bold"), url("../fonts/Raleway-Bold.woff2") format("woff2"), url("../fonts/Raleway-Bold.woff") format("woff");
}
@font-face {
  font-family: "Raleway";
  font-weight: 800;
  font-style: normal;
  src: local("Raleway ExtraBold"), url("../fonts/Raleway-ExtraBold.woff2") format("woff2"), url("../fonts/Raleway-ExtraBold.woff") format("woff");
}
@font-face {
  font-family: "Raleway";
  font-weight: 300;
  font-style: normal;
  src: local("Raleway ExtraLightItalic"), url("../fonts/Raleway-ExtraLightItalic.woff") format("woff2"), url("../fonts/Raleway-ExtraLightItalic.woff") format("woff");
}
html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
body {
  min-width: auto;
  margin: 0;
  font-family: "Raleway", "Arial", sans-serif;
  font-weight: 600;
  font-size: 18px;
}
a {
  text-decoration: none;
}
img {
  max-width: 100%;
  height: auto;
}
.container {
  max-width: 1535px;
  margin: 0 auto;
}
.container-main {
  max-width: 1345px;
  margin: 0 auto;
}
.title-section {
  font-family: "Raleway", "Arial", sans-serif;
  font-size: 30px;
  font-weight: 800;
  letter-spacing: 3.75px;
  text-align: center;
  color: #1d1d1d;
  text-transform: uppercase;
  margin: 0;
}
.welcome {
  background-image: url("../img/bg-image-welcome.png");
  background-repeat: no-repeat;
  background-position: center;
  background-color: #000000;
  padding-top: 117px;
  padding-bottom: 390px;
}
.welcome__logo {
  font-size: 30px;
  font-family: "Raleway", "Arial", sans-serif;
  font-weight: 800;
  letter-spacing: 3.75px;
  text-align: center;
  color: #ededed;
  text-transform: uppercase;
}
.welcome__title {
  font-family: "Raleway", "Arial", sans-serif;
  font-weight: 800;
  font-size: 48px;
  letter-spacing: 6px;
  text-align: right;
  color: #ededed;
  text-transform: uppercase;
}
.welcome__text {
  font-family: "Raleway", "Arial", sans-serif;
  font-weight: 400;
  font-size: 24px;
  line-height: 1.67;
  letter-spacing: 3px;
  text-align: right;
  color: #ededed;
  margin: 0;
}
.welcome__text-italic {
  font-family: "Raleway", "Arial", sans-serif;
  font-weight: 300;
  font-size: 18px;
  letter-spacing: 2.25px;
  margin: 0;
}
.main-header {
  margin: 30px 0;
}
.main-header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.main-header__logo {
  font-family: "Raleway", "Arial", sans-serif;
  font-size: 30px;
  font-weight: 800;
  letter-spacing: 3.75px;
  color: #1c1c1c;
  text-transform: uppercase;
  display: inline-block;
}
.main-header__nav {
  display: inline-block;
}
.main-header__nav-list {
  display: flex;
  list-style: none;
  justify-content: flex-end;
  margin: 0;
}
.main-header__nav-item {
  font-family: "Raleway", "Arial", sans-serif;
  font-size: 18px;
  font-weight: 600;
  letter-spacing: 2.25px;
  text-transform: uppercase;
  margin-left: 90px;
}
.main-header__nav-link {
  color: #3b3b3b;
}
.main-header__nav-link:hover {
  color: #1dda01;
}
.main-header__nav-link:active {
  color: #0e0e0e;
}
.about-us {
  background: linear-gradient(#f4f4f4 7%, #ffffff 93%);
  padding: 50px 0 70px;
}
.about-us__description {
  text-align: center;
}
.statistics {
  background-image: url("../img/background-color.png");
  background-repeat: no-repeat;
  background-size: cover;
  padding: 90px 0 100px;
  color: #ededed;
}
.statistics__title {
  color: #ededed;
}
.statistics__description {
  text-align: center;
  margin: 0;
}
.statistics__list {
  display: flex;
  justify-content: space-between;
  margin-top: 60px;
}
.statistics__nomber {
  font-size: 72px;
}
.statistics__point {
  font-size: 24px;
  text-transform: uppercase;
  text-align: center;
  max-width: 230px;
}
.statistics__item {
  margin: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  flex: 1 0 250px;
}
.catalog {
  margin: 50px 0 70px;
}
.catalog__description {
  letter-spacing: 2.25px;
  text-align: center;
}
.catalog__list {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 0;
}
.catalog__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex: 1 0 400px;
}
.catalog__item-title {
  font-size: 24px;
  font-weight: bold;
  letter-spacing: 3px;
  text-align: center;
  color: #3b3b3b;
  margin: 0;
  text-transform: uppercase;
  margin-bottom: 30px;
}
.catalog__item-price {
  display: block;
}
.catalog__item-link {
  color: #3b3b3b;
  border-bottom: 1px solid #3b3b3b;
}
.catalog__item-link:active {
  color: #1dda01;
}
.catalog__item-link:hover {
  color: #1dda01;
  text-shadow: 2px 2px 5px #ededed;
  border-bottom: none;
}
.team {
  background-color: #ededed;
  padding: 50px 0 50px;
}
.team__description {
  text-align: center;
}
.team__list {
  list-style: none;
  display: flex;
  justify-content: space-around;
  padding: 0;
}
.team__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-left: 250px;
}
.team__item:nth-child(1) {
  margin: 0;
}
.team__item-name {
  font-size: 30px;
  font-weight: 800;
  letter-spacing: 3.75px;
  color: #1e1e1e;
  text-transform: uppercase;
  margin: 0;
}
.team__item-profession {
  letter-spacing: 2.25px;
  text-transform: uppercase;
  margin: 0;
}
.galery {
  text-align: center;
  margin: 60px 0;
}
.galery__photo {
  display: flex;
}
.galery__subscribe-text {
  font-size: 24px;
  font-weight: 800;
  letter-spacing: 3px;
  text-transform: uppercase;
}
.main-footer {
  background-color: #1e1e1e;
  padding-top: 55px;
  padding-bottom: 90px;
}
.main-footer-container {
  display: flex;
  justify-content: space-between;
}
.main-footer__logo {
  font-size: 30px;
  font-weight: 800;
  letter-spacing: 3.75px;
  text-align: center;
  text-transform: uppercase;
  color: #ededed;
  display: block;
  margin-bottom: 145px;
}
.main-footer__logo:active {
  color: #1dda01;
}
.main-footer__logo:hover {
  color: #1dda01;
}
.main-footer__logo:focus {
  color: #0e0e0e;
}
.footer-nav {
  list-style: none;
  padding: 0;
  margin: 0;
}
.footer-nav__link {
  color: #ededed;
  font-size: 18px;
  font-weight: 600;
  letter-spacing: 2.25px;
  text-align: left;
  text-transform: uppercase;
}
.footer-nav__link:active {
  color: #1dda01;
}
.footer-nav__link:hover {
  color: #1dda01;
}
.footer-nav__link:focus {
  color: #0e0e0e;
}
.footer-nav__link-main {
  color: #ededed;
  font-size: 18px;
  font-weight: 900;
  letter-spacing: 2.25px;
  text-align: left;
  text-transform: uppercase;
}
.footer-nav__item-main {
  margin-bottom: 25px;
}
.footer-nav__item {
  margin-bottom: 10px;
}
.social-title {
  font-size: 18px;
  font-weight: 900;
  letter-spacing: 2.25px;
  text-align: left;
  color: #ededed;
  margin: 0;
  text-transform: uppercase;
  margin-bottom: 25px;
}
.social-item {
  background-repeat: no-repeat;
  width: 30px;
  height: 30px;
  display: inline-block;
}
.social-item--fb {
  background-image: url("../img/facebook.png");
}
.social-item--in {
  background-image: url("../img/linked-in.png");
}
.social-item--inst {
  background-image: url("../img/instagram.png");
}
.social-item--tw {
  background-image: url("../img/twitter.png");
}
